import React from 'react';
import './About.css';
import HemantPhoto from '../../assests/Images/hemant.jpg';

class About extends React.Component{

    render() {
        return(
            <div className='About'  ref={(div) => {this.refAbout = div;}}>
                <div className='AboutText'>
                    <p id='ourteam'>Our Team</p>
                </div>
                <div className='BoxContainerForImages1'>
                        <img src={HemantPhoto} alt='teampic' className='HemantPhoto'/>
                        <p className='HemantDescription'> Hemant Kumar</p> 
                </div>

            </div>
        );
    }

}
export default About;


