import React from 'react';
import './Contact.css';
class Contact extends React.Component{
    
    state = {
        emailEntered:'',
        message:'',
        subject:'',
        name:''
    }
    nameChangeHandler = (event) =>{
        this.setState({name:event.target.value})
    }
    subjectChangeHandler = (event) => {
        this.setState({subject:event.target.value})
    }
    emailCHangeHandler = (event) => {
        this.setState({email:event.target.value})
    }
    messageChangeHandler = (event) => {
        this.setState({message:event.target.value})
    }
    render(){
        return(
            <div className='Contact'>
              <div className='InnerContactDiv'>
                <div className='AddressDiv'>
                    <h2>CONTACT US</h2>
                    <p>PHONE : 9899095353</p>
                    <p>Hall-3,IIT Kanpur, India</p>
                  <div className='Sharing'>  
                    <a href='' className='GooglePlus'><i className="fa fa-google-plus-square" style={{color:'#000000'}}></i></a>
                    <a href='' className='facebookICON'><i className="fa fa-facebook-square" style={{color:'#000000'}}></i></a> 
                    <a href='' className='Github'><i className="fa fa-github" style={{color:'#000000'}}></i></a>
                    <a href='' className='Instagram'><i className="fa fa-instagram" style={{color:'#000000'}}></i></a> 
                    <a href='' className='Twitter'><i className="fa fa-twitter" style={{color:'#000000'}}></i></a>
                  </div>
                </div>
                <div className='MessageForm'>
                    <h2>Leave a Message</h2>
                    <input className='InputClassSendMessage' type='text' placeholder='YOUR NAME' onChange={this.nameChangeHandler}/>
                    <input className='InputClassSendMessage' type='text' placeholder='SUBJECT' onChange={this.subjectChangeHandler} />
                    <input className='InputClassSendMessage' type='text' placeholder='YOUR E-MAIL' onChange={this.emailCHangeHandler} />
                    <input className='MainMessage' type='text' placeholder='Your Message' onChange={this.messageChangeHandler} />
                    <button className='Reset'>Reset</button>
                    <button className='Send'>Send</button>
                </div>
              </div>  
            </div> 
        );
    }
}
export default Contact;


