import React from 'react';
import './Login.css';
import ForgotPassword from '../ForgotPassword/ForgotPassword';
class Login extends React.Component {
  state = {
    email:'',
    password:'',
    loginButtonPressed: false,
    showError: '',
    forgotpassword: false
  }
  inputHandler = (event) => {
      this.setState({email:event.target.value})
  }
  passwordHandler = (event) => {
    this.setState({password: event.target.value})
  }
  continueLoginHandler = () => {
    if(this.state.email.length > 0 && this.state.password.length > 5 
      && this.state.email.includes('@') ){
      fetch('https://quickcure.azurewebsites.net/login',{
        method:'post',
        headers: {'Content-Type' : 'application/json'},
        body: JSON.stringify({
          email : this.state.email,
          password: this.state.password
        })
      }).then( res => res.json()).then(data => {
        if(data.Response === true){
          console.log(data)
          this.props.toHandleLogInStateTrue(data);
        }else {
          this.setState({showError:'Wrong Credentials'})
        }
      })
    } else {
      this.setState({showError: 'Enter Your Credientials'})
    }
  }
  forgotPasswordHandler = () => {
      this.setState({forgotpassword: true,email: '',password:''})
  }
  CancelHandler = () => {
    this.setState({forgotpassword: false})
  }
  render() {
      if(this.state.forgotpassword === false){
        return (
          <div className='LoginOuterDiv'>
            {this.state.showError}
            <div><input type='email' placeholder='Enter Your e-mail Address' className='InputFieldLogin' onChange={this.inputHandler}/></div>
            <div><input type='password' placeholder=' Enter Your Password' className='PasswordFieldLogin' onChange={this.passwordHandler} /></div>
              <button  className='ToContinueLoginButton' onClick={this.continueLoginHandler}> Log In </button>
              <button className='ForgotPasswordButton' onClick={this.forgotPasswordHandler}> Forgot Password ? Click Here </button>
          </div>
        );  
      } else {
        return (
          <ForgotPassword CancelHandler = {this.CancelHandler}/>
        );
      }
        
  }
}
export default Login;




