import React from 'react';
import './SignUp.css';
let errdisplaydate = null;
let errdisplaynumber = null;
let errpassword = null;
class Sign extends React.Component {

    state = {
        email:'',
        name:'',
        gender:'',
        mobile:'',
        dob:'',
        choosedPassword:'',
        confirmPassword:'',
        errpass: true,
        AccountINFO: null,
        ToHandleSignupOuterDiv:'SignUpOuterDiv',
        ToShowError: ''
    }
    checkAllHandler = () => {
        if(this.state.email.length > 0 && this.state.name.length > 0 && this.state.gender.length > 0 && this.state.dob.length > 0 &&
            this.state.mobile.length === 10 && this.state.choosedPassword === this.state.confirmPassword){
                return true;
            } else {
                return false;
            }
    }

    nameInputHandler = (event) => {
        this.setState({name:event.target.value})
    }
    emailInputHandler = (event) => {
        this.setState({email:event.target.value})

    }
    onClickHandlerRadio = (event) => {
        this.setState({gender:event.target.value})

    }
    choosePasswordInputHandler = (event) => {
        this.setState({choosedPassword: event.target.value})
        if(this.state.confirmPassword !== event.target.value){
            errpassword = ' Passwords Do not match' 
            this.setState({errpass: true})
        } else {
            errpassword = ''
            this.setState({errpass: false})
        }
    }
    confirmPasswordInputHandler = (event) => {
        this.setState({confirmPassword:event.target.value})
        if(this.state.choosedPassword !== event.target.value){
            errpassword = ' Passwords Do not match' 
            this.setState({errpass: true})
        } else {
            errpassword = ''
            this.setState({errpass: false})
        }

    }

        dateHandler = (event) => {
            let currDate = Date();
            let dateEntered = event.target.value;
            let curryear = '';
            let yearentered = '';
            for(let i = 0 ; i < 4 ; i++) {
                curryear = curryear + currDate[11+i];
                yearentered = yearentered.concat(dateEntered[i])
            }
            curryear = parseInt(curryear,10);
            yearentered = parseInt(yearentered,10);   
               
            if(yearentered >= curryear ){
                errdisplaydate = 'Enter Correct data';
                this.setState({dob:''})
    
            } else{
                errdisplaydate = '';
                this.setState({dob: event.target.value})
            }
        }
    mobileInputHandler = (event) => {
        let mobnumber = event.target.value;
        let check = parseInt(mobnumber,10);
        if(check && mobnumber.length === 10){
            errdisplaynumber='';
            this.setState({mobile: event.target.value})
        } else {
            errdisplaynumber = 'Enter Correct mobile number';
            this.setState({mobile: ''})
        }
    }
    continueToCreate = () => {

        if(this.checkAllHandler()){
            fetch('https://quickcure.azurewebsites.net/signup',{
                method: 'post',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({
                    email:this.state.email,
                    name:this.state.name,
                    gender:this.state.gender,
                    mobile:this.state.mobile,
                    dob:this.state.dob,
                    password:this.state.choosedPassword
                })
            }).then(res => res.json()).then(data => {
                this.setState({AccountINFO: data.response,ToHandleSignupOuterDiv: 'HideSignUpOuterDiv'})
            })     
        } else{
            this.setState({ToShowError: 'Fill All Fields Correctly'});
        }
       
        
    }
    render() {
            if(this.state.ToHandleSignupOuterDiv === 'SignUpOuterDiv'){
                return(
                    <div className={this.state.ToHandleSignupOuterDiv}>
                        <p>{errdisplaydate}</p>
                        <p>{errdisplaynumber}</p>
                        <p>{errpassword}</p>
                        <p>{this.state.ToShowError}</p>
                        <input type='text' placeholder='Enter Your Name' className='InputFieldName' onChange={this.nameInputHandler} />
                        <input type='email' placeholder='Enter Your Email' className='SignEmail' onChange={this.emailInputHandler} />
                        <input type='text' placeholder='Enter Your Mobile Number' className='Mobile' onChange={this.mobileInputHandler} />
                        <div className='GenderParagraph'>          
                            Male : <input type ='radio' value='Male' name='usergender' onClick={this.onClickHandlerRadio} />
                            Female : <input type = 'radio' value='Female' name='usergender' onClick={this.onClickHandlerRadio}/> 
                            Others : <input type='radio' value='Other' name='usergender' onClick={this.onClickHandlerRadio}/>
                        </div>
                        <input type='date' className='dob' onChange={this.dateHandler} id='DateEnteredObject'/>
                        <input type='password' className='PasswordOne' placeholder='Choose Your Password' onChange={this.choosePasswordInputHandler}/>
                        <input type='password' className='PasswordTwo' placeholder='Confirm Your Password' onChange={this.confirmPasswordInputHandler}/>
                        <button onClick={this.continueToCreate} className='ContinueToCreate'> Continue </button>
                        <button className='ResetCreateAccount'> Reset </button>
                    </div>
                );
            } else{
                return(
                    <div className='Final Message'>
                        {this.state.AccountINFO}
                    </div>
                )
            }
                    

    }

}
export default Sign;


