import React from  'react';
import axios from 'axios';
import './ProfileCard.css';
class ProfileCard extends React.Component {
    state = {
        description: '',
        selectedProfileFile: '',
        selectedFile: null,
        ProfilePic: 'Not Changed',
        UploadMessage:'',
        List: [],
        imageofUser : 'https://quickcure.azurewebsites.net/'+(Math.floor((Math.random() * 1000) + 1))+'/uploads/patients/' + this.props.userData.userid+'/PatientsPic.jpg'
    }
    download = (event) => {
        let some = event.target.id;
        console.log(this.state.ListArrayObj[some - 1].ext);
        let U = this.state.ListArrayObj[some - 1].Path;
        const filename = 'file'+this.state.ListArrayObj[some - 1].ext;
        axios({
            url: U,
            method: 'GET',
            responseType: 'blob', // important
          }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download',filename);
            document.body.appendChild(link);
            link.click();
          });

    }
    onChangeprofile = (e) => {
        switch (e.target.name) {
          case 'selectedFile':
            this.setState({ selectedProfileFile: e.target.files[0] });
            break;
          default:
            this.setState({ [e.target.name]: e.target.value });
        }
      }  
    onSubmitprofile = (e) => {
        e.preventDefault();
        const {  selectedProfileFile } = this.state;
        let formData = new FormData();
        formData.append('selectedFile', selectedProfileFile);
        axios.post('https://quickcure.azurewebsites.net/uploading/'+this.props.userData.userid +'/'+ this.props.userData.upassword, formData)
          .then((result) => {
            if(result.data.Response === "File Uploaded"){    
                this.setState({ProfilePic: 'Changed',imageofUser: 'http://localhost:3000/'+(Math.floor((Math.random() * 10000) + 1))+'/uploads/patients/' + this.props.userData.userid+'/PatientsPic.jpg'})
            } else {
                this.setState({ProfilePic: 'File Not Uploaded'})
            } 
        });
    }
    onChange = (e) => {
        switch (e.target.name) {
          case 'selectedFile':
            this.setState({ selectedFile: e.target.files[0] });
            break;
          default:
            this.setState({ [e.target.name]: e.target.value });
        }
      }  
    onSubmit = (e) => {
        e.preventDefault();
        const { description, selectedFile } = this.state;
        let formDatas = new FormData();
        formDatas.append('description',description);
        formDatas.append('selectedFile', selectedFile);
        axios.post('https://quickcure.azurewebsites.net/uploads/file/'+this.props.userData.userid +'/'+ this.props.userData.upassword, formDatas)
          .then((result) => {
            this.setState({UploadMessage: result.data.Response})
        });
    }
    showUploadedFiles = () => {
        if(sessionStorage.getItem('iamquickcureuser')){
            let tempUserData = JSON.parse(sessionStorage.getItem('iamquickcureuser')).UserData;
            fetch('https://quickcure.azurewebsites.net/login/matchinghash',{
              method:'post',
              headers: {'Content-Type' : 'application/json'},
              body: JSON.stringify({
                email : tempUserData.uemail,
                password: tempUserData.upassword
              })
            }).then( res => res.json()).then(data => {
                    this.setState({ListArrayObj:data.ListOfFiles ,List: data.ListOfFiles.map((Obj) =>{return(<div key={Obj.id}><p>{Obj.FILES}</p>
                    <button className='downloadUploadedFile' onClick = {this.download} id = {Obj.id}>Download File</button></div>)})});
            })
          }
    }
    render() {
        const { description } = this.state;
        return(
            <div className='ProfileCard'>
              <div className='Imageninfo'>
                <div className='PatientImage'>
                    <img className='dp' key={this.state.number} src={this.state.imageofUser} height='180px' width='180px' alt='uim' />
                    <form id= 'dpselectionform' onSubmit={this.onSubmitprofile}>    
                        <div className='Choosedp'>
                            Choose pic
                            <input
                                    className='hide_file'
                                    type="file"
                                    name="selectedFile"
                                    onChange={this.onChangeprofile}
                            />
                        </div>
                        <button type="submit" className='changedpbtn'>Change</button>
                    </form>        
                </div>    
                <div className='BasicInfo'>
                    <p><div id='infotitle'>Name:</div> {this.props.userData.uname}</p>
                    <p><div id='infotitle'>Email:</div> {this.props.userData.uemail}</p>
                    <p><div id='infotitle'>Gender:</div> {this.props.userData.ugender}</p>
                    <p><div id='infotitle'>DOB:</div> {this.props.userData.udob}</p>
                </div>   
              </div>  
                <div className='UploadFiles'>
                        <p id='UploadHeading'>Upload and manage your medical reports,bills and much more..</p>
                        <form id='fileselectionform' onSubmit={this.onSubmit}>
                            <input
                                className='filedesc'
                                type="text"
                                name="description"
                                value={description}
                                placeholder='Name of file'
                                onChange={this.onChange}
                            />
                            <div id='ChooseFile'>  
                                Choose File  
                                <input
                                className='hide_file'
                                type="file"
                                name="selectedFile"
                                onChange={this.onChange}
                                />
                            </div>
                            <button id='uploadrecbtn' type="submit">Upload Files</button>
                         </form>
                    </div >
                    <div className='uploadmessage'>
                        {this.state.UploadMessage}
                    </div>
                    <button className='ViewUploadedbtn' onClick={this.showUploadedFiles}>See Uploaded files</button>
                    <div className='FilesListOfUser'> 
                        {this.state.List}
                    </div>
                
            </div>
        );
    }
}
export default ProfileCard;


