import React from 'react';
import SearchDoctor from '../SearchDoctor/SearchDoctor';
import CardFD from '../../components/Cards/CardFD';
import './MainFrameNav.css';
import Image1 from '../../assests/Images/uts2.jpg';
import Image2 from '../../assests/Images/check.jpg';
import SymptomSearch from '../SymptomSearch/SymptomSearch';
import scrollToComponent from  'react-scroll-to-component';
class MainFrameNav extends React.Component {
    state = {
        currentOption: 1,
        whattrend:'shoewhattrend',
        ForSearchDisease:'hideForSearchDoctor',
        ForSearchDoctor:'hideForSearchDisease',
        SearchDoctorref: null
    }
    componentDidUpdate(){
        if(this.state.ForSearchDoctor === 'showForSearchDoctor'){
            scrollToComponent(this.SearchDoctorref,{ offset: 0, align: 'middle', duration: 500, ease:'inQuad'});
        } else {
            scrollToComponent(this.SearchDisease,{ offset: 0, align: 'middle', duration: 500, ease:'inQuad'});
        }
    }
    OpenDivHandlerSearchDoctor = () => {
        this.CardHandler(2); 
    }
    CardHandler = (props) => {
        
        if(props === 1 ){
            this.setState({ currentOption: props,
                            whattrend : 'showwhattrend',
                            ForSearchDoctor :'hideForSearchDoctor',
                            ForSearchDisease :'hideForSearchDisease',
                            
                        })
        } else if ( props === 2 ){
            
            this.setState({ currentOption: props,
                whattrend : 'hidewhattrend',
                ForSearchDoctor :'showForSearchDoctor',
                ForSearchDisease :'hideForSearchDisease',
            });
            
        } else{
            this.setState({ currentOption: props,
                whattrend : 'hidewhattrend',
                ForSearchDoctor :'hideForSearchDoctor',
                ForSearchDisease :'showForSearchDisease'
            })
        }
    }
    render() {
        return(
            <div className='MostOuterDiv'>
                <div className='CardDiv'>
                    
                    <div onClick={() => { this.CardHandler(2);
                    }} className='FindDoctor'><CardFD img={Image1} txt='Find Best Doctors for you and Book Appointment With them' /></div>
                    <div onClick={() => this.CardHandler(3)} className='SearchD'><CardFD img={Image2} txt='Having troubles? Use our Symptom Checker to find the most probable disease'/></div>
                </div>
                <div ref={(div) => {this.SearchDoctorref = div;}} className={this.state.ForSearchDoctor}>
                    <SearchDoctor />
                </div>
                <div ref={(div) => {this.SearchDisease = div;}} className={this.state.ForSearchDisease}>
                    <SymptomSearch ToOpenSearchDoctorDiv={this.OpenDivHandlerSearchDoctor}/>
                </div>
            </div>
        );
    }
}
export default MainFrameNav;


