import React from 'react';
import './Form.css';
class Form extends React.Component{
    
    state = {
        key: '',
        newChoosePassword:'',
        newConfirmPassword:'',
        showMessagefromserver: '',
        showerror:'',
        aftersubmit: false
    }
    keyHandler = (event) => {
        this.setState({key: event.target.value})
    }
    newConfirmPasswordHandler = (event) =>{
        this.setState({newConfirmPassword: event.target.value})
    }
    newChoosePasswordHandler = (event) => {
        this.setState({newChoosePassword: event.target.value})
    }
    onSubmit = () => {
        
        if(this.state.key.length === 8
            && this.state.newChoosePassword === this.state.newConfirmPassword){
                console.log(this.state)
            fetch('https://quickcure.azurewebsites.net/checkkey',{
                method: 'post',
                headers: {"Content-Type":"application/json"},
                body:JSON.stringify({ 
                    key: this.state.key,
                    choosedpassword: this.state.newChoosePassword,
                    confirmpassword: this.state.newConfirmPassword,
                    email: this.props.Email
               
            })}).then(res => res.json()).then(data => {
                if(data.Response === 'Password Changed'){
                    this.setState({showMessagefromserver: 'Password Changed',aftersubmit: true})
                } else {
                    this.setState({showMessagefromserver: 'Wrong Key', aftersubmit: true})
                }
            })
            
        } else {
            this.setState({showerror: 'enter correct data'})
        }
    }
    render(){
        
        if(this.state.aftersubmit === true){
            return(<p>{this.state.showMessagefromserver}</p>)
        } else {
            return(
                <div className='Form'>
                    <p className='EmailCheckText'> Check Your E-mail and Enter Your Secret Key</p>
                    {this.state.showerror}
                    <input type='password'
                           className='InputFieldForgotPass'
                           placeholder='Secret Key' 
                           onChange={this.keyHandler}
                        />
                    <input type='password'
                           className='InputFieldForgotPass'
                           placeholder='Choose Password' 
                           onChange={this.newChoosePasswordHandler}
                        />
                    <input type='password'
                           className='InputFieldForgotPass'
                           placeholder='Confirm Password' 
                           onChange={this.newConfirmPasswordHandler}
                        />    
                    <button className='SubmitKeyButton' onClick={this.onSubmit}> Submit </button> 
                </div>
            );
        }

    }
}
export default Form;


