import React from 'react';
import Select from 'react-virtualized-select';
import axios from 'axios';
import ShowDisease from '../Showdisease/ShowDisease';
import './SymptomSearch.css';
class SymptomSearch extends React.Component {
    state = {
        currentSymptoms: [],
        symptoms: [],
        showerr: '',
        predicteddiseases:[]
    }
    componentWillMount() {
        fetch('https://quickcure.azurewebsites.net/searchdata').then(res => res.json()).then(data => {
            this.setState({symptoms: data.symptoms});
        })
    }
    onClickHandlerSymptom = () => {
        if(this.state.currentSymptoms.length > 0){
            let arr = this.state.currentSymptoms.map(sym => {
                return({id:sym.id})
            });
            console.log(arr);
            axios.post('https://quickcure.azurewebsites.net/symptom_check',{
                    arr: arr
            }).then(res => {
                console.log(res.data)
                let array = res.data.map(dis => {

                    return(<ShowDisease key={dis.data.id} name={dis.data.name}
                                 aboutdisease={dis.data.about}
                                 prevention={dis.data.prevention}
                                 symptoms={dis.data.symptoms}
                                 treatement={dis.data.treatment}
                                 TypeOfDisease={dis.message}
                    />)
                })
                this.setState({predicteddiseases: [<button className='InSymptomBookAppt' onClick={this.props.ToOpenSearchDoctorDiv}>Need to Book an Appointment?Click here.</button>,array]})
                console.log(this.state)
                
            })
        } else {
            this.setState({showerr: 'Enter Your Symptoms First'})
        }
    }
    handleInputSymptoms = (symptom) => {
        
        this.setState({currentSymptoms: symptom})
        console.log(this.state.currentSymptoms);
    }
    render() {
        return(
            <div className ='SymptomSearch'>
            <p className='Symptomsearch_text'>Begin by selecting your symptoms.(Enter a max of five symptoms) </p>
            {this.state.showerr}
              <div className='SymptomSelectnButton'>
                <div className='SelectingSymp'>
                    <Select style={{width:'360px',height:'40px'}}
                            name="Symptoms"
                            value={this.state.currentSymptoms}
                            labelKey='sym_desc'
                            valueKey='sym_desc'
                            options={this.state.symptoms}
                            filterOptions={this.state.symptoms}
                            onChange={this.handleInputSymptoms.bind(this)}
                            multi={true}
                            autosize={true}
                    />
                </div>    
                <button className='SearchSymptom' onClick = {this.onClickHandlerSymptom}>Search</button>
              </div>
            <div className='ListOfDiseases'>

                {this.state.predicteddiseases}
            </div>
        </div>
        );
    }
}
export default SymptomSearch;


