import React from 'react';
import './ShowDisease.css';
class ShowDisease extends React.Component {

    state = {
        Info: 'hideInfo',
        aboutdisease: 'hideaboutdisease',
        prevention:'hideprevention',
        symptoms:'hidesymptoms',
        treatement:'hidetreatement',
        TypeOfDisease:'hidetypeofdisease',
        symptomsshow: null
    }
    handleAboutDisease = () => {
        if(this.state.aboutdisease === 'hideaboutdisease'){
            this.setState({aboutdisease: 'showaboutdisease'})
        } else {
            this.setState({aboutdisease: 'hideaboutdisease'})
        }
    }
    handlePrevention = () => {
        if(this.state.prevention === 'hideprevention'){
            this.setState({prevention: 'showprevention'})
        } else {
            this.setState({prevention: 'hideprevention'})
        }
    }    
    handleSymptoms = () => {
        if(this.state.symptoms === 'hidesymptoms'){
            this.setState({symptoms: 'showsymptoms',symptomsshow: this.props.symptoms.map(sym => <li id='SympExplained'>{sym}</li>)});
        } else {
            this.setState({symptoms: 'hidesymptoms'})
        }
    }    
    handleTreatement = () => {
        if(this.state.treatement === 'showtreatement'){
            this.setState({treatement: 'hidetreatement'})
        } else {
            this.setState({treatement: 'showtreatement'})
        }
    }   
    handleTypeOfDisease = () => {
        if(this.state.TypeOfDisease === 'hidetypeofdisease'){
            this.setState({TypeOfDisease: 'showtypefdisease'})
        } else {
            this.setState({TypeOfDisease: 'hidetypeofdisease'})
        }
    }
    handleInfo = () => {
        if(this.state.Info === 'hideInfo'){
            this.setState({Info: 'showInfo'})
        } else{
            this.setState({Info: 'hideInfo'})
        }
    }
    render(){
        return(
            <div className='Disease'>
                <p id='DiseaseName' onClick={this.handleInfo} >{this.props.name}</p>
                <div className={this.state.Info}>
                    <p id='aboutdisease' onClick={this.handleAboutDisease}>About Disease</p>    
                    <div className={this.state.aboutdisease}>
                        <p className='explained'>{this.props.aboutdisease}</p>
                    </div> 
                    <p id='SympDetails' onClick={this.handleSymptoms}>Symptoms</p>
                    <div className={this.state.symptoms}>
                        <p className='explained'>{this.state.symptomsshow}</p>
                    </div>
                    <p id='Prevention' onClick={this.handlePrevention}>Prevention</p>
                    <div className={this.state.prevention}>
                        <p className='explained'>{this.props.prevention}</p>
                    </div>
                    
                    <p id='Treatment' onClick={this.handleTreatement}>Treatement</p>
                    <div className={this.state.treatement}>
                        <p className='explained'>{this.props.treatement}</p>
                    </div>
                </div>
            </div>
        );
    }

}
export default ShowDisease;


