import React from 'react';
import Login from '../Login/Login';
import scrollToComponent from  'react-scroll-to-component';
import SignUp from '../SignUp/SignUp';
import Modal from '../Modal/Modal';
import windowSize from 'react-window-size';
import './Navbar.css';
import ProfileCard from '../ProfileCard/ProfileCard';
class Navbar extends React.Component {
    state = {
        display: 'none',
        refresh: false
    }
    onCLickHandlerfafa = () => {
        if(this.state.display === 'none'){
            this.setState({display:'block'})
        } else {
            this.setState({display: 'none'})
        }
    }
    render() {
        if(this.props.tellCurrentLoggedInState()){
            if(this.props.windowWidth > 1513){
                return(
                <div className='NavbarInApp'>
                    <i className='fas fa-bars' id ='fafaIcon' style={{fontSize:'30px'}} onClick={this.onCLickHandlerfafa}></i>
                        <div className='NavbarMainClass' id='fafaNavbar' style={{display:'block'}}>
                                <p className='LogoName'>QuikCure</p>
                                <div className='NavbarHome'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refhome,{ offset: 0, align: 'top', duration: 1000, ease:'inQuad'})}>Home</button></div>
                                <div className='NavbarSick'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Find Your Sickness</button></div>
                                <div className='NavbarBook'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Book Appointment</button></div>
                                <div className='NavbarAbout'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refabout,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>About Us</button></div>
                                <div className='NavbarContact'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refcontact,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Contact Us</button></div>          
                                <div className='LogInButtonInsideApp'><Modal title='View Your Profile'><ProfileCard App = {this.props.App} ListOfFiles={this.props.ListOfFiles} userData ={this.props.userData}/></Modal></div>
                                <div className='SignUpButtonInsideApp'><button className='LogOutButton' onClick={this.props.toHandleLogInFalse}>Log Out</button></div>
                    </div>
                </div>
                );
            } else{
                return(
                    <div className='NavbarInApp'>
                        <p className='LogoName'>QuikCure</p>
                        <i className='fas fa-bars' id ='fafaIcon' style={{fontSize:'30px'}} onClick={this.onCLickHandlerfafa}></i>
                            <div className='NavbarMainClass' id='fafaNavbar' style={this.state}>
                                    <div className='NavbarHome'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refhome,{ offset: 0, align: 'top', duration: 1000, ease:'inQuad'})}>Home</button></div>
                                    <div className='NavbarSick'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Find Your Sickness</button></div>
                                    <div className='NavbarBook'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Book Appointment</button></div>
                                    <div className='NavbarAbout'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refabout,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>About Us</button></div>
                                    <div className='NavbarContact'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refcontact,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Contact Us</button></div>          
                                    <div className='LogInButtonInsideApp'><Modal title='View Your Profile'><ProfileCard App = {this.props.App} ListOfFiles={this.props.ListOfFiles} userData ={this.props.userData} /></Modal></div>
                                    <div className='SignUpButtonInsideApp'><button className='LogOutButton' onClick={this.props.toHandleLogInFalse}>Log Out</button></div>
                        </div>
                    </div>
                );

            }

        } else {
            if(this.props.windowWidth > 1513){
                return(
                <div className='NavbarInApp'>
                    <i className='fas fa-bars' id ='fafaIcon' style={{fontSize:'30px'}} onClick={this.onCLickHandlerfafa}></i>
                        <div className='NavbarMainClass' id='fafaNavbar' style={{display:'block'}}>
                                <p className='LogoName'>QuikCure</p>
                                <div className='NavbarHome'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refhome,{ offset: 0, align: 'top', duration: 1000, ease:'inQuad'})}>Home</button></div>
                                <div className='NavbarSick'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Find Your Sickness</button></div>
                                <div className='NavbarBook'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Book Appointment</button></div>
                                <div className='NavbarAbout'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refabout,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>About Us</button></div>
                                <div className='NavbarContact'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refcontact,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Contact Us</button></div>          
                                <div className='LogInButtonInsideApp'><Modal title='Log In'><Login  
                                                                                                    toHandleLogInStateTrue = {this.props.toHandleLogInStateTrue}/></Modal></div>
                                <div className='SignUpButtonInsideApp'><Modal title='Sign Up'><SignUp /></Modal></div>
                    </div>
                </div>
                );
            } else{
                return(
                    <div className='NavbarInApp'>
                        <p className='LogoName'>QuikCure</p>
                        <i className='fas fa-bars' id ='fafaIcon' style={{fontSize:'30px'}} onClick={this.onCLickHandlerfafa}></i>
                            <div className='NavbarMainClass' id='fafaNavbar' style={this.state}>
                                    <div className='NavbarHome'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refhome,{ offset: 0, align: 'top', duration: 1000, ease:'inQuad'})}>Home</button></div>
                                    <div className='NavbarSick'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Find Your Sickness</button></div>
                                    <div className='NavbarBook'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.reffindSick,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Book Appointment</button></div>
                                    <div className='NavbarAbout'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refabout,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>About Us</button></div>
                                    <div className='NavbarContact'><button className='NavbarButton' onClick={() => scrollToComponent(this.props.refcontact,{ offset: 0, align: 'middle', duration: 1000, ease:'inQuad'})}>Contact Us</button></div>          
                                    <div className='LogInButtonInsideApp'><Modal title='Log In'><Login  
                                                                                                        toHandleLogInStateTrue = {this.props.toHandleLogInStateTrue}/>
                                                                         </Modal>
                                    </div>
                                    <div className='SignUpButtonInsideApp'><Modal title='Sign Up'><SignUp /></Modal></div>
                        </div>
                    </div>
                );

            }
        }
    }
}
export default windowSize(Navbar);


