import React from 'react';
import axios from 'axios';
import './SearchDoctor.css';
import Select from 'react-virtualized-select';
import 'react-select/dist/react-select.css';
import 'react-virtualized/styles.css';
import 'react-virtualized-select/styles.css';
import DoctorCard from '../DoctorCard/DoctorCard';
class SearchDoctor extends React.Component {
    state = {
        Location:[],
        Fee:[],
        Speciality:[],
        Available: null,
        currentLocation:'',
        specialitySelected:'',
        AvaliabilitySelected:'',
        FeeSelected:'',
        ListOfDoctor:[],
        ListOfDoctorDiv: 'hideListOfDoctorDiv'
    }
    componentWillMount() {
        axios.get('https://quickcure.azurewebsites.net/searchdata')
        .then( res => {
            this.setState({Location:res.data.location,Fee: res.data.fee,Speciality: res.data.speciality});
        });
    }
    applyChangesHandler = () => {
        axios.post('https://quickcure.azurewebsites.net/searchdoctor',{
            location:this.state.currentLocation,
            fee: this.state.FeeSelected,
            speciality: this.state.specialitySelected
        }).then(res => {
            if(res.data.length > 0){
                this.setState({ListOfDoctor: res.data.map(doctor => 
                    <DoctorCard key={doctor.doc_id}
                                source={'https://quickcure.azurewebsites.net'+doctor.image}
                                doctornumber={doctor.doc_id}
                                Name={doctor.name}
                                Speciality={doctor.speciality}
                                Experience={doctor.experience}
                                available={doctor.timeslot}
                                Email={doctor.email}
                                Fee={doctor.fee}
                                Address={doctor.address}/>),ListOfDoctorDiv: 'showListOfDoctorDiv'})
            } else {
                this.setState({ListOfDoctor: []})
            }
        }).catch(err => {
            console.log(err)
        })
    }
    handleInputFee = (searchresult) => {
        this.setState({FeeSelected:searchresult})
    }

    handleInputLocation = (searchresult) => {
        this.setState({currentLocation:searchresult})
    }

    handleInputSpeciality= (searchresult) => {
        this.setState({specialitySelected:searchresult})
    }
    render() {
        return(
            <div className='ForBlockDisplay'>
                 
                <div className='wid'>
                <div className='ChooseFilters'>
                    Let us refine your search!
                </div>   
                        <div className='Location'>
                            <p>Location</p> 
                            <Select
                                name="Location"
                                value={this.state.currentLocation}
                                labelKey='location'
                                valueKey='location'
                                options={this.state.Location}
                                filterOptions={this.state.Location}
                                onChange={this.handleInputLocation.bind(this)}
                                multi={false}
                                autosize={false}
                            />
                        </div>
                        <div className='Speciality'>
                            <p> Speciality</p>
                            <Select
                                name="Speciality"
                                value={this.state.specialitySelected}
                                labelKey='speciality'
                                valueKey='speciality'
                                options={this.state.Speciality}
                                filterOptions={this.state.Speciality}
                                onChange={this.handleInputSpeciality.bind(this)}
                                multi={false}
                                autosize={false}
                            />
                        </div>
                        <div className='Fee'> 
                            <p>Fee Range</p>
                            <Select
                                name="Fee"
                                value={this.state.FeeSelected}
                                labelKey='fee_range'
                                valueKey='fee_range'
                                options={this.state.Fee}
                                filterOptions={this.state.Fee}
                                onChange={this.handleInputFee.bind(this)}
                                multi={false}
                                autosize={false}
                            />
                        </div>
                        <button className='ApplyFilterButton' onClick={this.applyChangesHandler}> Search Doctors </button>
                    </div>
                    <div className={this.state.ListOfDoctorDiv}>
                            {this.state.ListOfDoctor}
                    </div>

                </div>

        );
    }

}
export default SearchDoctor;


