import React from 'react';
import Form from '../Form/Form';
import './ForgotPassword.css';
class ForgotPassword extends React.Component {

    state = {
        email: '',
        Form: false,
        showerr: ''
    }
    InputHandler = (event) => {
        this.setState({email:event.target.value})
    }
    ConfirmHandler = () => {
        if(this.state.email.includes('@')){
            fetch('https://quickcure.azurewebsites.net/forgotpassword/resetingrequest/'+this.state.email)
            .then(res => res.json()).then(data => {
                if(data.Response === 'MailSent'){
                    this.setState({Form: true});
                } else {
                    this.setState({showerr: 'Enter Correct Email'})
                }
            })
        }
    }
    CancelHandler = () => {
        this.setState({email: ''})
        this.props.CancelHandler();
    }
    render() {
        if(this.state.Form === false){
            return(
                <div className='ForgotPassword'>
                    {this.state.showerr}
                    <input type='email' 
                        className='InputEmailForgotPassword' 
                        placeholder='Enter Your Email'
                        onChange={this.InputHandler}
                    />
                    <button className='Confirmbutton' onClick={this.ConfirmHandler}> Confirm </button>
                    <button className='CancelForgotPassword' onClick={this.CancelHandler}>Cancel</button>
                </div>
            );
        } else {
           return(<Form Email = {this.state.email}/>)
        }
    }

}
export default ForgotPassword;


