import React from 'react';
import './DetailsForBookAppointment.css';
class Details extends React.Component{

    state = {
        nameEntered:'',
        addressEntered:'',
        cellNumberEntered:'',
        emailEntered:'',
        gender:'',
        errinNumber: false,
        message:''
    }
    nameChangeHandler = (event) => {
        this.setState({nameEntered: event.target.value})
    }
    emailChangeHandler = (event) => {
        this.setState({emailEntered:event.target.value})
    }
    addressChangeHandler = (event) => {
        this.setState({addressEntered: event.target.value})
    }
    cellChangeHandler = (event) => {
        let cellnumber = event.target.value;
        let check = parseInt(cellnumber,10);
        if(check && cellnumber.length === 10){
            this.setState({cellNumberEntered: event.target.value, errinNumber: false})
         
        } else {
            this.setState({cellNumberEntered: '', errinNumber: true}) 
        }

    }
    CheckErrorInState = () => {
        console.log(this.state)
        if(this.state.nameEntered.length > 0 && this.state.errinNumber === false && this.state.addressEntered.length > 0 && this.state.emailEntered.length > 0 &&this.state.gender.length > 0){
            return true;
        } else {
            return false;
        }
    }
    ContinueToBookAppointment = () => {
   
        if(this.CheckErrorInState){
            fetch('https://quickcure.azurewebsites.net/bookingappointment',{
                method:'post',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({
                    email: this.state.emailEntered,
                    phone: this.state.cellNumberEntered,
                    address: this.state.addressEntered,
                    gender: this.state.gender,
                    name: this.state.nameEntered,
                    doctorInfo: this.props.DoctorInfo
                })
            }).then(res => res.json()).then(data => {
                this.setState({message: data.Response})
            })

        } else{
            console.log('wrong')
        }
    }
    genderHandler = (event) => {
        this.setState({gender: event.target.value})
    }
    render(){
        if(this.state.message.length > 0){
           return( 
                <div>
                     {this.state.message} 
                </div>
            )
        } else {
            return(
                <div className='DetailsForm'>
                    <input id='appointmentformdetails' onChange={this.nameChangeHandler} type='text' placeholder='Enter Your Name' />
                    <input id='appointmentformdetails' onChange={this.emailChangeHandler}type='email' placeholder='Your Email' />
                    <input id='appointmentformdetails' onChange={this.cellChangeHandler} type='text' placeholder='Your Phone Number'/>
                    <div className='gendertext'>Select Your gender:</div>
                    <div className='genderStyling'>
                        Male : <input onChange= {this.genderHandler}type='radio' value='Male' name='Usergender' />
                        Female : <input onChange={this.genderHandler}type='radio' value='Female' name='Usergender' />
                        Others : <input onChange={this.genderHandler}type='radio' value='Female' name='Usergender' />
                    </div>
                    <input id='AppointmentAddress' onChange={this.addressChangeHandler} type= 'text' placeholder='Your Address'/>
                    <button onClick={this.ContinueToBookAppointment} className='ContinueToBookAppointment'> Confirm </button>
                    <button onClick={this.CancelHandler} className='CancelAppointment'> Cancel </button>
                </div>
            )
        }
    }
}
export default Details;


