import React from 'react';
import './DoctorCard.css';
import Modal from '../Modal/Modal';
import Details from '../DetailsForBookAppointment/DetailsForBookAppointment';
class DoctorCard extends React.Component{

    render(){
        return(
            <div className='DoctorID'>
                <div className='DoctorPhoto'>
                    <img alt ='doctorimage' src={this.props.source} className='DoctorPhotoRound'/>
                </div>
                <div className='DoctorINFO'>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Name :</div> {this.props.Name}</div>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Speciality : </div>{this.props.Speciality}</div>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Experience :</div> {this.props.Experience}</div>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Email :</div> {this.props.Email}</div>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Fee :</div> {this.props.Fee}</div>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Availiability :</div> {this.props.Availiability}</div>
                    <div className='InfoParaClass'><div className='DocInfoTitle'>Address :</div> {this.props.Address}</div>
                </div>
                <Modal title='Book Appointment'>
                    <Details DoctorInfo={this.props}/>
                </Modal>
            </div>
        );
    }
        
}
export default DoctorCard;


